@import 'ScssHelpers/helpers-all.scss';

.related_artciels {
	.card_pagination & {
		padding: 0;
	}

	@include bp($point_2, min) {
		padding: 9rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 5.6rem 2rem 7.6rem;

		&:not(.body--color_mod &, .card_pagination &) {
			background-color: $green;
		}
	}

}

.related_artciels__header {
	text-align: center;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 5rem;
		}

		@include bp($point_3) {
			margin-bottom: 3.2rem;
		}
	}

	h6 {
		@include subtitle;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.5rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.2rem;
			}
		}
	}

	h2 {
		font-family: $font_2;

		@include h2;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.5rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.2rem;
			}
		}
	}

	p {
		max-width: 52rem;
		margin: 0 auto;

		@include text;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.5rem;
			}

			@include bp($point_3) {
				margin-bottom: 1.2rem;
			}
		}
	}
}

.related_artciels__footer {
	@include bp($point_3) {
		padding-bottom: 3.6rem;
	}
}

.related_artciels__body {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;

	@include bp($point_2, min) {
		margin: 0 -2rem -3.2rem 0;
	}

	@include bp($point_3) {
		margin: 0 -1.4rem -3.2rem 0;

		overflow: visible;
	}
}

.related_artciels__card {
	width: 100%;

	@include bp($point_2, min) {
		padding: 0 2rem 3.2rem 0;

		.related_artciels__4_col_mod & {
			flex: 0 0 25%;
		}

		.related_artciels__3_col_mod & {
			flex: 0 0 33.333%;
		}

		.related_artciels__2_col_mod & {
			flex: 0 0 50%;
		}
	}

	@include bp($point_3) {
		max-width: 30.4rem;

		height: auto;
		padding: 0 1.4rem 3.2rem 0;

		.card_pagination & {
			max-width: 35rem;
		}
	}
}

.related_artciels__wrapper {
	position: relative;

	display: flex;
	flex-direction: column;

	height: 100%;

	border-radius: $gl_radius;

	text-align: center;

	color: $black_2;
	background-color: $white_2;

	transition: box-shadow 0.6s ease;

	overflow: hidden;

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_2, min) {
		&:hover {
			box-shadow: 3px 3px 4rem rgba(0, 0, 0, 0.12);

			.related_artciels__img {
				transform: scale(1.1);
			}

			.related_artciels__btn strong {
				font-weight: 700;
				border-bottom: 2px solid $black_2;
			}
		}
	}

	@include bp($point_3) {
		box-shadow: 3px 3px 4rem rgba(0, 0, 0, 0.12);

		.related_artciels__content strong {
			font-weight: 700;
			border-bottom: 2px solid $black_2;
		}
	}

	h4 {
		font-family: $font_2;

		@include h4;

		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1rem;
			}

			@include bp($point_3) {
				margin-bottom: 0.6rem;
			}
		}

		@include bp($point_2, min) {
			.related_artciels__4_col_mod & {
				font-size: 2.8rem;
				line-height: 120%;
			}

			.related_artciels__3_col_mod & {
				margin-bottom: 2rem;

				font-size: 3.2rem;
				line-height: 110%;
			}

			.related_artciels__2_col_mod & {
				margin-bottom: 2rem;

				font-size: 5.6rem;
				line-height: 110%;
			}
		}
	}

	p {
		display: block;

		flex: 1 1 100%;

		margin: 0 auto;

		max-width: 29rem;

		@include text;
		letter-spacing: 0.02em;

		@include bp($point_2, min) {
			.related_artciels__3_col_mod & {
				margin-bottom: 2rem;
			}

			.related_artciels__2_col_mod & {
				margin-bottom: 2rem;
			}
		}
	}

	strong {
		color: $black_2;
		font-weight: 500;
		line-height: 1.3;
		letter-spacing: 0.02em;

		@include bp($point_2, min) {
			font-size: 1.5rem;
		}

		@include bp($point_3) {
			font-size: 1.3rem;

			font-weight: 700;
			border-bottom: 2px solid #141414;
		}
	}
}

.related_artciels__btn {
	@include bp($point_2, min) {
		padding: 0 3.2rem 3.2rem;
	}

	@include bp($point_3) {
		padding: 0 2.4rem 2.4rem;
	}
}

.related_artciels__content {
	flex: 1 1 100%;

	height: 100%;

	@include bp($point_1, min) {
		padding: 4rem 3.2rem 2rem 3.2rem;
	}

	@include bp($point_1 - 1, $point_2) {
		padding: 2rem 1.4rem 2rem 1.4rem;
	}

	@include bp($point_3) {
		padding: 2.2rem 2.4rem 0.8rem 2.4rem;
	}
}

.related_artciels__picture {
	position: relative;

	display: block;

	overflow: hidden;

	@include bp($point_2, min) {
		.related_artciels__4_col_mod & {
			padding-bottom: 82.8%;
		}

		.related_artciels__3_col_mod & {
			padding-bottom: 81.4%;
		}

		.related_artciels__2_col_mod & {
			padding-bottom: 66.4%;
		}
	}

	@include bp($point_3) {
		padding-bottom: 89.2%;

		.card_pagination & {
			padding-bottom: 75%;
		}
	}
}

.related_artciels__img {
	position: absolute;
	top: 0;
	left: 0;

	display: block;

	width: 100%;
	height: 100%;

	object-fit: cover;

	transition: transform 0.6s ease;
}

.related_artciels__button {
	width: 100%;

	text-align: center;

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: block;
	}
}

.related_artciels__more {
	width: 18.1rem;

	@include btn_size_normal;
	@include btn_style_white;
}
