@import 'ScssHelpers/helpers-all.scss';

.page_nav {
	width: 100%;

	padding: 0 var(--gl-indent);
	margin-top: -4.8rem;

	z-index: 10;

	@include bp($point_3) {
		display: none;
	}
}

.page_nav--offset_mod {
	margin-bottom: 7.6rem;
}

.page_nav__body {
	background: $white_2;

	padding: 0 5.2rem;

	border: 2px solid $black_2;
	border-radius: $gl_radius;

	box-shadow: 1rem 1rem 4rem rgba(0, 0, 0, 0.2);
}

.page_nav__list {
	display: flex;
	align-items: center;
	justify-content: space-around;

	flex-wrap: wrap;

	text-align: center;
}

.page_nav__item {
	position: relative;

	height: 100%;
}

.page_nav__link {
	display: flex;
	align-items: center;
	flex-direction: column;

	height: 100%;

	font-weight: 400;
	font-family: $font_2;
	white-space: nowrap;
	font-size: 3.2rem;
	line-height: 1.2;
	text-decoration: none;

	color: #000;

	@include bp($point_1, min) {
		padding: 2.8rem 3.8rem;
	}

	@include bp($point_1 - 1) {
		padding: 2.8rem 1.6rem;
	}

	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 50%;

		transform: translateX(-50%);

		display: block;

		height: 0.6rem;
		max-width: 23.6rem;
		width: 100%;

		background-color: #000;

		opacity: 0;

		transition: opacity 0.3s ease;
	}

	&:before {
		content: attr(data-text);
		height: 0;
		visibility: hidden;
		overflow: hidden;
		user-select: none;
		pointer-events: none;
		font-style: italic;
		font-weight: 700;

		@media speech {
			display: none;
		}
	}

	&:focus {
		font-weight: 700;
		font-style: normal;

		&:after {
			opacity: 1;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@media (any-hover: hover) {
		&:hover {
			// font-style: italic;
			font-weight: 700;

			&:after {
				opacity: 1;
			}
		}
	}
}
