@import 'ScssHelpers/helpers-all.scss';

.content_module {
	@include bp($point_2, min) {
		padding: 8rem var(--gl-indent) 18rem;
	}

	@include bp($point_3) {
		padding: 8rem var(--gl-indent);
	}
}

.content_module__row {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, stretch);
	}
}

.content_module__col {
	@include bp($point_2, min) {
		width: 50%;
	}
	@include bp($point_3) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.content_module__icon_w {
	@include bp($point_2, min) {
		margin-bottom: 3.2rem;
		width: 8.3rem;
		height: 4.4rem;
	}
	@include bp($point_3) {
		margin-bottom: 1.5rem;
		width: 5.1rem;
		height: 3.2rem;
	}
}

.content_module__icon {
	@include contain_img;
}

.content_module__subtitle {
	width: 100%;

	text-transform: uppercase;
	font-size: 1.6rem;
	font-weight: 700;
	letter-spacing: 0.2em;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.content_module__title {
	@include h2;

	font-family: $font_2;
	width: 100%;
}

.content_module__title_w {
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		padding-left: 14.2rem;
		max-width: 73.6rem;
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, center, center, center);

		text-align: center;
	}
}

.content_module__info_w {
	width: 100%;
	height: 100%;

	@include bp($point_2, min) {
		// padding: 0 0 0 14.8rem;
		padding-left: min(14.8rem, 8.1vw);
		max-width: 67.5rem;
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, center, center, center);

		text-align: center;
	}
}

.content_module__info_title {
	@include h3;

	font-family: $font_2;
	font-style: italic;
	font-weight: 400;
	line-height: 118%;

	&:not(:last-child) {
		margin-bottom: 2.5rem;
	}
}

.content_module__info_descr {
	@include text;

	&:not(:last-child) {
		margin-bottom: 4rem;
	}

	p:not(:last-child) {
		margin-bottom: 1rem;
	}
}

.content_module__info_price {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6.4rem;
		}
		@include bp($point_3) {
			margin-bottom: 4rem;
		}
	}

	@include bp($point_3) {
		max-width: 60rem;
		width: 100%;
	}
}

.content_module__info_price_title {
	width: 100%;

	text-transform: uppercase;
	font-size: 1.6rem;
	font-weight: 700;

	@include bp($point_2, min) {
		letter-spacing: 0.2em;

		&:not(:last-child) {
			margin-bottom: 4.6rem;
		}
	}
	@include bp($point_3) {
		letter-spacing: 0.1em;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.content_module__info_price_list {
	@include bp($point_3) {
		padding: 0 2.2rem;
		text-align: left;
	}
}

.content_module__info_price_row_w {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}

		&.content_module__info_price_row_w--offset_mod {
			margin-bottom: 4.4rem;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}

		&.content_module__info_price_row_w--offset_mod {
			margin-bottom: 4rem;
		}
	}
}

.content_module__info_price_row {
	@include flex_block(row, nowrap, flex-start, flex-start, stretch);
	margin-right: -2rem;
}

.content_module__info_price_col {
	width: 50%;

	padding-right: 2rem;
}

.content_module__info_price_col_style {
	line-height: 140%;
	letter-spacing: 0.02em;

	.content_module__info_price_col:last-child & {
		text-align: right;

		@include bp($point_2, min) {
			font-weight: 500;
		}
		@include bp($point_3) {
			font-weight: 400;
		}
	}

	@include bp($point_2, min) {
		font-size: 2.2rem;
		font-weight: 700;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
		font-weight: 500;
	}
}

.content_module__info_price_col_including {
	line-height: 140%;
	letter-spacing: 0.02em;

	.content_module__info_price_col:first-child & {
		font-style: italic;
	}

	.content_module__info_price_col:last-child & {
		text-align: right;

		@include bp($point_2, min) {
			font-weight: 500;
		}
		@include bp($point_3) {
			font-weight: 400;
		}
	}

	@include bp($point_2, min) {
		font-size: 2.2rem;
	}
	@include bp($point_3) {
		font-size: 1.8rem;
	}
}

.content_module__info_btn_w {
	width: 100%;

	@include bp($point_3) {
		@include flex_block(row, wrap, center, center, center);
	}
}
