@import 'ScssHelpers/helpers-all.scss';

.gallery__title {
	@include h2;

	max-width: 78rem;
	width: 100%;
	margin-left: auto;
	margin-right: auto;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.5rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}

	font-family: $font_2;
	text-align: center;
}

.gallery__descr {
	max-width: 79rem;
	width: 100%;
	margin: 0 auto;

	font-weight: 400;
	letter-spacing: 0.03em;

	text-align: center;

	p:not(:last-child) {
		margin-bottom: 1rem;
	}

	@include bp($point_2, min) {
		font-size: 1.8rem;
		line-height: 130%;
	}
	@include bp($point_3) {
		padding: 0 2rem;
		font-size: 1.6rem;
		line-height: 137%;
	}
}

.gallery__heading {
	@include bp($point_2, min) {
		margin-bottom: 6.4rem;
	}
	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

.gallery__list_drag {
	position: relative;

	@include bp($point_2, min) {
		height: calc(min(26vw, 44rem) * 2 + 2.4rem);
		// position: absolute;
	}
}

.gallery__list_w {
	@include bp(1640px, min) {
		margin-right: calc(-1 * ((100vw - $page_width) / 2));
	}

	@include bp(1639px) {
		margin-right: calc(-1 * var(--gl-indent));
		padding-bottom: 4rem;
	}

	@include bp($point_2, min) {
		position: absolute;
	}
}

.gallery__list {
	// position: absolute;

	@include bp($point_2, min) {
		// position: absolute;
		display: grid;

		grid-template-rows: min(24vw, 42rem) min(24vw, 42rem);
		// grid-template-rows: min(25.2vw, 45rem) min(24vw, 44rem);
		grid-auto-columns: min(21.4vw, 38.5rem);
		grid-auto-flow: row dense;
		grid-gap: 2rem;

		// padding-bottom: 4.4rem;

		transition: transform ease 0.2s;

		// overflow-x: auto;

		// &::-webkit-scrollbar {
		// 	height: 0.6rem;
		// 	width: 0rem; /* ширина всей полосы прокрутки */
		// }

		// &::-webkit-scrollbar-track {
		// 	background: rgba(255, 255, 255, 0); /* цвет зоны отслеживания */
		// 	border-bottom: 1px solid $black_2;
		// }

		// &::-webkit-scrollbar-thumb {
		// 	background-color: $black_2; /* цвет бегунка */
		// 	border-radius: 0; /* округлось бегунка */
		// }
	}
}

.gallery__item {
	@include bp($point_2, min) {
		&:nth-child(4n + 1) {
			grid-column: span 2;
			grid-row: 1 / span 2;
		}
		&:nth-child(4n + 2) {
			grid-row: 1;
			grid-column: span 2;
		}
	}

	@include bp($point_3) {
		padding-right: 1.5rem;
		margin-bottom: 2.6rem;
	}
}

.gallery__item_link {
	display: block;
	height: 100%;
}

.gallery__item_in {
	border-radius: 0.8rem;
	overflow: hidden;

	height: 100%;
}

.gallery__item_img_w {
	position: relative;
	display: block;
	height: 100%;

	@include bp($point_3) {
		padding-bottom: 101%;
	}
}

.gallery__item_img {
	@include cover_img;

	transition: transform ease 0.3s;

	.gallery__item_link:hover & {
		transform: scale(1.1);
	}

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.gallery__scrollbar_w {
	@include bp($point_2, min) {
		display: none;
	}
	@include bp($point_3) {
		position: relative;

		width: calc(100% - var(--gl-indent));
		height: 0.5rem;

		overflow: hidden;

		.swiper-scrollbar {
			bottom: 1px !important;
		}
	}
}

.gallery__scrollbar_desk_w {
	@include bp($point_2, min) {
		position: absolute;
		bottom: 0;
		left: 0;

		display: none;

		height: 0.5rem;
		width: 100%;
		border-bottom: 1px solid $black_2;
		overflow-x: hidden;
	}
}

.gallery__scrollbar_desk {
	@include bp($point_2, min) {
		width: 71%;
		height: 100%;

		background: linear-gradient(to right, $black_2 0%, $black_2 41%, rgba(252, 252, 252, 0) 10%);
		transition: transform ease 0.3s;
	}
}
